import { mapState, mapMutations } from 'vuex';
import {getOpenId, getUser, registerUser} from '@/api/user';
export default {
	computed:{
		...mapState(["code"])
	},
	mounted(){
		uni.setNavigationBarTitle({
		　　title:'健康生活'
		})
	},
	onLoad(){
		//this.login(true);
	},
	data:function(){
		return {
			currentUser:{}
		}
	},
	methods:{
		...mapMutations(["setCode", "setCurrentUser"]),
		phone_login(){
			uni.navigateTo({
				url:'./phoneLogin'
			})
			
		},
		onGetUserInfo(res){
			if(res.detail && res.detail.userInfo){
				this.$store.commit("setCurrentUser", res.detail.userInfo)
				this.currentUser = res.detail.userInfo;
				this.login();
				uni.showLoading({
					title: '登录中...'
				});
			}
		},
		login(flag){
			if(flag){
				uni.getUserInfo({
					provider:"weixin",
					success:res=>{
						this.currentUser = {
							...this.currentUser,
							...res.userInfo
						}
					}
				})
			}
			uni.login({
				provider:"weixin",
				success:res=>{
					this.currentUser.code = res.code;
					getOpenId(res.code).then(res=>{
						let str = res.data.replace(/\'/g, '\"');
						str = str.substr(0, str.indexOf('}')+1)
						let data = JSON.parse(str)
						this.currentUser.OpenId = data.openid;
						return getUser(data.openid);
					}).then(res=>{
						if(res.data){
							this.currentUser = {
								...this.currentUser,
								...res.data
							};
							this.$store.commit('setCurrentUser', this.currentUser)
							uni.hideLoading()
							uni.switchTab({
								url:'../index/index'
							})
						} else {
							if(!flag){
								return registerUser({
									"OpenId":this.currentUser.OpenId,
									"Name":this.currentUser.nickName,
									"PSW":"123",
									"PhoneNumber":"123456"
								})
							}
						}
					}).then(res=>{
						this.currentUser = {
							...this.currentUser,
							...res.data
						};
						this.$store.commit('setCurrentUser', this.currentUser)
						uni.hideLoading()
						uni.switchTab({
							url:'../index/index'
						})
					}).catch(res=>{
						uni.hideLoading();
					})
				},
			});
		},
	}
}
